import "@tensorflow/tfjs";
import * as bodyPix from "@tensorflow-models/body-pix";
import {
  BodyPix,
  ModelConfig,
} from "@tensorflow-models/body-pix/dist/body_pix_model";

export enum ProcessingMode {
  BLUR,
  BACKGROUND,
}

// little endian
const empty = 255 << 24;

export default class Model {
  private readonly options: ModelConfig | undefined;
  private net: BodyPix | null;
  public inited: boolean;
  private raf: number | null;
  private background: Uint32Array;

  constructor(options?: ModelConfig) {
    /*
    architecture: "ResNet50",
    outputStride: 32,
    quantBytes: 1,
    * */
    this.options = options;
    this.net = null;
    this.raf = null;

    this.inited = false;
    this.background = new Uint32Array(1);
  }

  async load() {
    const self = this;

    await new Promise<void>((resolve) => {
      const img = new Image();
      img.onload = () => {
        const canvas = document.createElement("canvas");
        const { width, height } = img;
        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext("2d");
        if (!ctx) return;
        ctx.drawImage(img, 0, 0);
        const data = ctx.getImageData(0, 0, width, height).data;
        self.background = new Uint32Array(data.buffer);
        resolve();
      };
      img.src = `${process.env.PUBLIC_URL}/background.jpeg`;
    });

    console.time("load model");
    this.net = await bodyPix.load(this.options);
    this.inited = true;
    console.timeEnd("load model");
  }

  stop() {
    cancelAnimationFrame(this.raf as number);
  }

  async process(
    stream: MediaStream,
    mode: ProcessingMode
  ): Promise<MediaStream> {
    if (!this.net) throw new Error("net is not ready");
    if (!this.background) throw new Error("no background");

    if (this.raf) {
      this.stop();
      this.raf = null;
    }

    const self = this; // üòè

    /*
     * In order to proceed with the stream we need:
     * 1. Render this stream in <video>
     * 2. Render frames in 1st <canvas> (using requestAnimationFrame)
     * 3. Process the original image
     * 4. Render output image in 2nd <canvas>
     * 5. Capture the stream from the 2nd canvas
     * */

    const video = document.createElement("video");
    video.muted = true;
    video.srcObject = stream;
    await new Promise<void>((resolve) => {
      video.addEventListener("loadedmetadata", () => {
        video.play();
        resolve();
      });
    });

    const width = video.videoWidth;
    const height = video.videoHeight;

    const original = document.createElement("canvas");
    original.width = width;
    original.height = height;
    const originalCtx = original.getContext("2d");

    if (!originalCtx) throw new Error("failed to get context from canvas");

    const processed = document.createElement("canvas");
    processed.width = width;
    processed.height = height;
    const processedCtx = processed.getContext("2d");

    if (!processedCtx) throw new Error("failed to get context from canvas");

    async function render() {
      // @ts-ignore
      originalCtx.drawImage(video, 0, 0, width, height);
      // @ts-ignore
      const frame = originalCtx.getImageData(0, 0, width, height);

      // @ts-ignore
      const segmentation = await self.net.segmentPerson(frame, {
        flipHorizontal: true,
        internalResolution: "medium",
        segmentationThreshold: 0.7,
      });

      const data = new ArrayBuffer(frame.data.length);
      const uint8 = new Uint8ClampedArray(data);
      const u32 = new Uint32Array(data);

      const o = new Uint32Array(frame.data.buffer);

      for (let i = 0; i < segmentation.data.length; i++) {
        const r = segmentation.data[i] === 0;

        u32[i] = r
          ? mode === ProcessingMode.BACKGROUND
            ? self.background[i]
            : empty
          : o[i];
      }

      // @ts-ignore
      processedCtx.putImageData(new ImageData(uint8, width, height), 0, 0);

      self.raf = requestAnimationFrame(render);
    }

    this.raf = requestAnimationFrame(render);

    // @ts-ignore
    return processed.captureStream(25);
  }
}
