import React, {
  useCallback,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import { nanoid } from "nanoid";
import { useHistory } from "react-router-dom";
import {
  Box,
  Button,
  Divider,
  Fab,
  Grid,
  TextField,
  Typography,
} from "@material-ui/core";
import ForumIcon from "@material-ui/icons/Forum";
import { request } from "./socket";
import { makeStyles } from "@material-ui/core/styles";
import VideocamOffIcon from "@material-ui/icons/VideocamOff";

import BlurOnIcon from "@material-ui/icons/BlurOn";
import ImageIcon from "@material-ui/icons/Image";

import StreamContext from "./StreamContext";
import getStream from "./getStream";
import ModelContext from "./ModelContext";
import { ProcessingMode } from "./model";

const useStyles = makeStyles((theme) => ({
  title: {
    marginBottom: theme.spacing(2),
  },
  actions: {
    marginTop: theme.spacing(2),
  },
  create: {
    marginRight: theme.spacing(2),
  },
  join: {
    marginLeft: theme.spacing(2),
  },
  video: {
    width: "640px",
    height: "480px",
    backgroundColor: "rgb(0, 0, 0)",
  },
  player: {
    width: "100%",
    height: "100%",
  },
  button: {
    margin: theme.spacing(1),
  },
}));

function Home() {
  const styles = useStyles();
  const history = useHistory();

  const [room, setRoom] = useState("");
  const [showJoinButton, setShowJoinButton] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [disabled, setDisabled] = useState(true);

  const [blur, setBlur] = useState(false);
  const [background, setBackground] = useState(false);

  const model = useContext(ModelContext);

  const video = useRef<HTMLVideoElement | null>(null);
  const { stream, setStream } = useContext(StreamContext);

  useEffect(() => {
    if (stream) return;

    getStream()
      .then((s) => {
        setStream(s);
      })
      .catch(console.error);
  }, []);

  useEffect(() => {
    if (!stream) return;
    if (video.current) {
      video.current.srcObject = stream;
    }
  }, [stream]);

  useEffect(() => {
    if (!model) return;
    if (!model.inited) {
      model.load().then(() => setDisabled(false));
    } else {
      setDisabled(false);
    }
  }, []);

  useEffect(() => {
    setError(false);

    if (!room.length) setShowJoinButton(false);
    else setShowJoinButton(true);
  }, [room]);

  const join = useCallback(async () => {
    const status = await request({ event: "join", room });

    if (status) {
      history.push(`/${room}`);
    } else {
      setError(true);
    }
  }, [history, room]);

  const create = useCallback(async () => {
    const room = nanoid();
    const status = await request({ event: "create", room });

    if (status) {
      history.push(`/${room}`);
    }
  }, [history]);

  function handleBlur() {
    if (!model || !stream || !model.inited) return;

    setBlur(!blur);
    setBackground(false);

    if (!blur) {
      model.process(stream, ProcessingMode.BLUR).then(setStream);
    } else {
      getStream().then(setStream);
    }
  }

  function handleBackground() {
    if (!model || !stream || !model.inited) return;

    setBlur(false);
    setBackground(!background);

    if (!background) {
      model.process(stream, ProcessingMode.BACKGROUND).then(setStream);
    } else {
      getStream().then(setStream);
    }
  }

  return (
    <Grid container>
      <Grid container item sm={6} alignItems="center" justify="flex-end">
        <Grid container item sm={10} direction="column">
          <Grid container direction="column" className={styles.title}>
            <Typography variant="h5">
              The most secure video conference powered by WebRTC
            </Typography>
            <Typography variant="subtitle1">
              Create new room or join the existing one
            </Typography>
          </Grid>
          <Divider />
          <Grid container item className={styles.actions}>
            <Button
              disabled={loading}
              variant="contained"
              className={styles.create}
              startIcon={<ForumIcon />}
              onClick={create}
            >
              New room
            </Button>
            <TextField
              error={error}
              disabled={loading}
              variant="outlined"
              label="Enter room id"
              value={room}
              onChange={(e) => setRoom(e.target.value)}
            />
            {showJoinButton && (
              <Button disabled={loading} className={styles.join} onClick={join}>
                Join
              </Button>
            )}
          </Grid>
        </Grid>
      </Grid>
      <Grid
        container
        item
        sm={6}
        justify="center"
        alignItems="center"
        direction="column"
      >
        <Box
          className={styles.video}
          color="text.primary"
          m={1}
          display="flex"
          justifyContent="center"
          alignItems="center"
        >
          {loading && <VideocamOffIcon fontSize="large" />}
          <video
            ref={video}
            onLoadedData={() => setLoading(false)}
            className={styles.player}
            style={{ display: loading ? "none" : "block" }}
            muted
            autoPlay
          />
        </Box>
        <Grid>
          <Fab
            color={blur ? "primary" : "default"}
            className={styles.button}
            disabled={disabled}
            onClick={handleBlur}
          >
            <BlurOnIcon fontSize="large" />
          </Fab>
          <Fab
            color={background ? "primary" : "default"}
            className={styles.button}
            disabled={disabled}
            onClick={handleBackground}
          >
            <ImageIcon fontSize="large" />
          </Fab>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Home;
